import React from 'react'
import { connect } from 'react-redux';
import { components } from 'react-select';
import PropTypes from 'prop-types'
import store from '../../../redux/store';
import { SET_INSTANCE_CREATION_MODAL } from '../../../redux/actions/types';



const CustomSelectMenu = (props) => {

    let activeDropdownMenu;

    //findout which fetch options is to be used
    for (let key in props.instanceCreationModal) {
        if (props.instanceCreationModal[key].isActive) {
            activeDropdownMenu = key;
            break;
        }
    }

    console.log('---------------------------------')
    // console.log(activeDropdownMenu)
    // console.log(props.instanceCreationModal)


    return <components.Menu {...props} className="CustomReactSelectMenu">
        {props.children}
        <button
            style={{ marginBottom: 0, height: '36px', borderRadius: '0 0 4px 4px', borderLeft: 'none', borderRight: 'none', borderBottom: 'none' }}
            className="btn btn-outline-primary btn-sm  btn-block"
            onClick={() => {
                store.dispatch({
                    type: SET_INSTANCE_CREATION_MODAL,
                    payload: [activeDropdownMenu, true]
                })
            }}
        ><b> <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</b></button>
    </components.Menu>
}




CustomSelectMenu.propTypes = {
    instanceCreationModal: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
    instanceCreationModal: state.instanceCreationModal
})


export default connect(mapStateToProps, {})(CustomSelectMenu);