import isEmpty from "./isEmpty"


const validatePaymentCreation = (paymentData) => {


    const errors = {}


    if (isEmpty(paymentData.accountId)) {
        errors.accountId = 'This field cannot be empty!'
    }

    if (isEmpty(paymentData.amount)) {
        errors.amount = 'This field cannot be empty!'
    }
    if (isEmpty(paymentData.method)) {
        errors.method = 'Select one of the payment method!'
    }

    if (paymentData.methodHeader === 'bank' && isEmpty(paymentData.bankName)) {
        errors.bankName = 'Provide Bank name!'
    } else if (errors.bankName) {
        delete (errors.bankName)
    }

    if ((paymentData.methodHeader === 'bank' || paymentData.methodHeader === 'transfer') && isEmpty(paymentData.transactionId)) {
        errors.transactionId = 'Provide transaction ID!';
    } else if (errors.transactionId) {
        //delete txid
        delete (errors.transactionId)
    }



    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}



