import React from 'react'
import Num from '../../App/components/Reusables/Num';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import IsEmpty from '../../App/components/Reusables/IsEmpty'
import { Row, Col, Card, Table, Tabs, Tab, Badge, Modal, Button, Collapse } from 'react-bootstrap';
import styled from "styled-components";
import DeleteModal from '../../App/components/Reusables/DeleteModal';



const IconButtonWrapper = styled.div`
  float: right;
  transform: rotate(0deg);
  overflow: hidden;
  transition: all 0.3s ease-out;
  transform: ${props => (props.rotate ? `rotate(90deg)` : "")};
`;



function Payments(props) {
    const [showPayments, setShowPayments] = React.useState(false);
    const [deleteData, setDeleteData] = React.useState({ showModal: false, paymentId: null })

    const _triggerDeleteModal = (id) => setDeleteData({ ...deleteData, showModal: true, paymentId: id })

    const _deletePayment = (idToBeDeleted) => {
        props.deletePayment({ "action": 'delete', payments_ids: [idToBeDeleted] })
            .then(async (res) => {
                if (res.json.status) {
                    const deleted = props.paymentsArr.find(instance => instance.id === idToBeDeleted);
                    let tempArr = props.paymentsArr;
                    tempArr.splice(tempArr.indexOf(deleted), 1);
                    await props.setPaymentsArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, paymentId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Payment deleted successfully!', background: '#dffff0' })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }

            }).catch((err) => {
                console.log(err)
            });
    }

    const PaymentsList = props.paymentsArr
        .filter((payment, index) => !payment.is_paid_credit)
        .map((payment, index) => {
            return <tr key={index}>
                <th scope="row">{payment.id}</th>
                <td>{payment.amount}</td>
                <td>{payment.payment_method[0].name[0].toUpperCase() + payment.payment_method[0].name.slice(1)}</td>
                <td>
                    {payment.payment_method[0].header === 'credit' ?
                        <Badge
                            className="btn btn-info btn-sm shadow-1"
                            onClick={() => props._payForCredit(payment)}
                        > Do Payment</Badge> :
                        null}
                </td>
                <td>
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={(e) => {
                            _triggerDeleteModal(payment.id)
                        }}
                    />
                </td>
            </tr>
        })



    return (
        <Card>
            <Modal.Header
                onClick={() => setShowPayments(!showPayments)}
                style={{ margin: 'auto 5px', border: 'none', cursor: 'pointer', display: 'flex', 'justifyContent': 'space-between' }}
            >
                <Modal.Title
                    style={{ paddingLeft: '10px', color: '#3d4075', fontSize: '1.1em', fontWeight: 'bold', }}
                >
                    Payments
                    </Modal.Title>

                <Modal.Title
                    style={{ paddingLeft: '10px', color: '#3d4075', fontSize: '1.1em', fontWeight: 'bold', }}
                >
                    <IconButtonWrapper rotate={showPayments ? 1 : 0}>

                        <i className="feather icon-chevron-right text-c-white f-15" />
                    </IconButtonWrapper>
                </Modal.Title>
            </Modal.Header>
            <Collapse in={showPayments}>
                <div id="basic-collapse" >
                    <Card.Body className='paymentsCard'>
                        <Table responsive>
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Amount</th>
                                    <th>Method</th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {props.paymentsArr.length > 0 ?
                                    PaymentsList :
                                    <tr><td colSpan={4}>Data not available!</td></tr>
                                }
                            </tbody>
                        </Table>

                    </Card.Body>
                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _deletePayment(deleteData.paymentId)}
                    />
                </div>
            </Collapse>
        </Card>
    )
}

export default Payments;