



import React from 'react';
import { Row, Col, Card, Table } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import { NavLink } from 'react-router-dom';

import Jhilikka from '../../App/components/Reusables/Jhilikka';
import IsEmpty from '../../App/components/Reusables/IsEmpty'
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import { getPurchaseOrder, deletePurchaseOrder, updatePurchaseOrder } from '../../api/inventory/purchaseOrder';
import Num from '../../App/components/Reusables/Num';
import CreatePaymentModal from '../Payments/CreatePaymentModal';
import { getPayment, deletePayment } from '../../api/payment/payment';
import Payments from '../Payments/Payments';
import PaymentInfo from '../../App/components/Reusables/PaymentInfo';





const PurchaseOrder = (props) => {


    const [loading, setLoading] = React.useState(true);
    const [purchaseOrderData, setPurchaseOrderData] = React.useState({});
    const [purchaseItemsData, setPurchaseItemsData] = React.useState([]);
    const [paymentsArr, setPaymentsArr] = React.useState([])
    const [createPaymentData, setCreatePaymentData] = React.useState({ showModal: false, creditPaymentFor: null, creditPaymentAmount: null });


    const [deleteData, setDeleteData] = React.useState({ showModal: false, purchaseOrderId: null, })

    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            let ladder = 0;
            await getPurchaseOrder({ action: 'get', purchase_order_id: props.match.params.id }, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        await setPurchaseOrderData({ ...res.json.p_order[0] });
                        await setPurchaseItemsData([...res.json.p_items]);
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
            await getPayment({
                "action": "get",
                "filter": "purchase_order",
                "purchase_order": props.match.params.id,
            }, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        await setPaymentsArr([...res.json.payments])
                        ladder++
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
            console.log(ladder);
            if (ladder === 2) {
                setLoading(false)
            }
        })()
        return () => controller.abort();
    }, [])

    const _triggerDeleteModal = (e) => {
        setDeleteData({ showModal: true, purchaseOrderId: props.match.params.id });
    }

    const _payForCredit = (payment) => {
        setCreatePaymentData({ ...createPaymentData, showModal: true, creditPaymentFor: payment.id, creditPaymentAmount: payment.amount })
    }


    const _deletePurchaseOrder = (idToBeDeleted) => {
        deletePurchaseOrder({ action: 'delete', purchase_orders_id: [idToBeDeleted] })
            .then(async (res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Purchase order deleted successfully!', background: '#dffff0' })
                    props.history.push('/purchase-orders')
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }

    const _markReceived = async () => {
        const updateOrderJson = {
            'action': 'update',
            'purchase_order_id': purchaseOrderData.id,
            'third_party_invoice_number': purchaseOrderData.third_party_invoice_number,
            'invoiced_on': purchaseOrderData.invoiced_on,
            'completed_on': purchaseOrderData.completed_on,
            'discount_type': purchaseOrderData.discount_type,
            'discount': purchaseOrderData.discount,
            'bill_received': true,
            'vendor': purchaseOrderData.vendor,
            'status': purchaseOrderData.status,
        }

        console.log(JSON.stringify(updateOrderJson))

        await updatePurchaseOrder({ ...updateOrderJson })
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Purchase order marked received!', background: '#dffff0' })
                    setPurchaseOrderData({ ...purchaseOrderData, bill_received: true })
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch(err => {
                console.log(err);
            })
    }






    const PurchaseItemsList = purchaseItemsData.map((item, index) => {
        return (
            <tr
                key={index}>
                <th scope="row">{item.id}</th>

                <td>{item.item_name}</td>
                <td>{item.non_discount_price}</td>
                <td>{item.quantity}</td>
                <td>{item.defective}</td>
                <td><Num>{item.non_discount_price * item.quantity}</Num></td>
                <td>{item.discount}{item.discount_type === 'percent' ? '%' : null}</td>
                <td><Num>{item.purchase_price * item.quantity}</Num></td>
                <td>{item.status}</td>
            </tr>
        )
    });

    const orderTotal = purchaseOrderData.discount_type === 'percent' ?
        purchaseOrderData.total_cost - 0.01 * purchaseOrderData.discount * purchaseOrderData.total_cost :
        purchaseOrderData.total_cost - purchaseOrderData.discount


    const dueAmount = purchaseOrderData.bill_amount - purchaseOrderData.paid_amount;
    console.log('due amount is ', dueAmount);

    const completedOn = (new Date(purchaseOrderData.completed_on)).toDateString()
    const invoicedOn = (new Date(purchaseOrderData.invoiced_on)).toDateString()
    let paymentStatus = 'Not paid';
    if (purchaseOrderData.paid_amount > 0 && purchaseOrderData.paid_amount < purchaseOrderData.bill_amount) paymentStatus = 'Partially Paid'
    if (dueAmount <= 0) paymentStatus = 'Paid'

    if (loading) return <Loader />
    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" >Purchase Order</h4>
                <div>
                    <button
                        className="btn btn-danger btn-sm shadow-2 mb-4"
                        onClick={_triggerDeleteModal}
                    > <i className="feather icon-trash-2 text-c-black f-15 m-r-5" />Delete</button>
                </div>
            </Row>



            <Row >
                <Col md={12}>
                    <Card>
                        <Card.Body>
                            <VerticalTimeline animate={false} layout={'1-column'}>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--education"
                                    date={`Status: Created on ${invoicedOn}`}
                                    iconStyle={{ background: '#12a9f4', color: '#fff', margin: 'auto', textAlign: 'center' }}
                                    icon={<i className="fa fa-pencil-square fa-2x" />}
                                >
                                    <h5 className="vertical-timeline-element-subtitle">Order Creation</h5>
                                    <button
                                        onClick={() => props.history.push(`/purchase-order/update/${props.match.params.id}`)}
                                        className="btn btn-outline-primary btn-sm mt-2 mb-0  p-1 pr-3 pl-3"
                                    >
                                        Edit
                                    </button>
                                    <br />
                                </VerticalTimelineElement>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--education"
                                    date={`Status: ${purchaseOrderData.bill_received ? `Received` : 'Not Received Yet'}`}
                                    iconStyle={{ background: '#be81dc', color: '#fff', margin: 'auto', textAlign: 'center' }}
                                    icon={<i className="fa fa-envelope fa-2x" />}
                                >
                                    <h5 className="vertical-timeline-element-subtitle">Receive Order Bill </h5>
                                    <button
                                        disabled={purchaseOrderData.bill_received}
                                        className="btn btn-outline-primary btn-sm mt-2 mb-0  p-1 pr-3 pl-3"
                                        onClick={_markReceived}
                                    >
                                        {purchaseOrderData.bill_received ? `Received` : 'Mark Received'}
                                    </button>
                                    <br />
                                </VerticalTimelineElement>
                                <VerticalTimelineElement
                                    className="vertical-timeline-element--education"
                                    date={`Status: ${paymentStatus}`}
                                    iconStyle={{ background: '#20c997', color: '#fff', margin: 'auto', textAlign: 'center' }}
                                    icon={<i className="fa fa-money fa-2x" />}
                                >
                                    <h5 className="vertical-timeline-element-subtitle">Payment</h5>


                                    <button
                                        disabled={dueAmount <= 0}
                                        className="btn btn-outline-primary btn-sm mt-2 mb-0  p-1 pr-3 pl-3"
                                        onClick={() => setCreatePaymentData({ ...createPaymentData, showModal: true })}
                                    >
                                        {dueAmount <= 0 ? 'Paid' : 'Add Payment'}
                                    </button>
                                    <br />
                                </VerticalTimelineElement>
                            </VerticalTimeline>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>

            <Row>
                <Col xl={12}>
                    <Card>
                        <PaymentInfo billAmount={purchaseOrderData.bill_amount} paidAmount={purchaseOrderData.paid_amount} />
                        <Card.Body>
                            <div className="row d-flex align-items-center">
                                <Col md={2}></Col>
                                <Col md={8}>




                                    <div className="d-flex justify-content-between">
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Vendor Name</b></div>
                                            <div className='mt-1 mb-3'>
                                                <NavLink to={`/vendor/${purchaseOrderData.vendor}`}>
                                                    <b>{purchaseOrderData.vendor_name}</b>
                                                </NavLink>
                                            </div>
                                        </div>
                                        <div className="w-50 ml-4">
                                            <div className='mb-1 border-bottom text-right'><b>Third Party Invoice Number</b></div>
                                            <div className='mt-1 mb-3 text-right'>{purchaseOrderData.third_party_invoice_number ? purchaseOrderData.third_party_invoice_number : 'NA'}</div>
                                        </div>
                                    </div>


                                    <div className="d-flex justify-content-between">
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Invoiced on</b></div>
                                            <div className='mt-1 mb-3'>{(new Date(purchaseOrderData.invoiced_on)).toDateString()}</div>
                                        </div>
                                        <div className="w-50 ml-4">
                                            <div className='mb-1 border-bottom  text-right'><b>Completed on</b></div>
                                            <div className='mt-1 mb-3 text-right'>{(new Date(purchaseOrderData.completed_on)).toDateString()}</div>
                                        </div>
                                    </div>

                                    <div className="d-flex justify-content-between" >
                                        <div className="w-50">
                                            <div className='mb-1 border-bottom'><b>Order Status</b></div>
                                            <div className='mt-1 mb-3'>{purchaseOrderData.status_name}</div>
                                        </div>
                                        <div className="w-50 ml-4">
                                            <div className='mb-1 border-bottom text-right'><b>Added By</b></div>
                                            <div className='mt-1 mb-3 text-right'>{purchaseOrderData.added_by_name ? purchaseOrderData.added_by_name : 'NA'}</div>
                                        </div>
                                    </div>

                                </Col>
                                <Col md={1}></Col>
                            </div>

                            <Card.Header>
                                <Card.Title as="h5">Purchase Items</Card.Title>
                            </Card.Header>
                            {purchaseItemsData.length > 0 ? (
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>#</th>
                                            <th>Item Name</th>
                                            <th>Price</th>
                                            <th>Quantity</th>
                                            <th>Defective</th>
                                            <th>Sub Total</th>
                                            <th>Discount</th>
                                            <th>Total</th>
                                            <th>Status</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {PurchaseItemsList}
                                        <tr >
                                            <td></td>
                                            <td colSpan={6}><b>Sub Total</b></td>
                                            <td colSpan={2}><b>Rs. <Num>{purchaseOrderData.total_cost}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={5}><b>Extra Discount</b></td>
                                            <td colSpan={1}><b>{purchaseOrderData.discount}{purchaseOrderData.discount_type === 'percent' ? '%' : null}</b></td>
                                            <td colSpan={2}><b>Rs. <Num>{purchaseOrderData.total_cost - orderTotal}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Paid Amount</b></td>
                                            <td colSpan={2}><b>Rs. <Num>{purchaseOrderData.paid_amount}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Total</b></td>
                                            <td colSpan={2}><b>Rs. <Num>{orderTotal}</Num></b></td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td colSpan={6}><b>Due</b></td>
                                            <td colSpan={2}><b>Rs. <Num>{dueAmount}</Num></b></td>
                                        </tr>
                                    </tbody>

                                </Table>
                            ) :
                                <IsEmpty>Purchase Items' list is empty!</IsEmpty>}

                        </Card.Body>
                    </Card>
                </Col>
                <Col md={6}>
                    <Payments
                        paymentsArr={paymentsArr}
                        deletePayment={deletePayment}
                        setPaymentsArr={setPaymentsArr}
                        _payForCredit={_payForCredit}
                    />
                </Col>



                <CreatePaymentModal
                    show={createPaymentData.showModal}
                    purchaseOrder={purchaseOrderData}
                    dueAmount={dueAmount}
                    creditPaymentFor={createPaymentData.creditPaymentFor}
                    creditPaymentAmount={createPaymentData.creditPaymentAmount}
                    onHide={() => setCreatePaymentData({ ...createPaymentData, showModal: false, creditPaymentFor: null, creditPaymentAmount: null })}
                />

                <DeleteModal
                    show={deleteData.showModal}
                    onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                    onDelete={() => _deletePurchaseOrder(deleteData.purchaseOrderId)}
                />
            </Row>
        </>
    );
}

export default PurchaseOrder;
