import React from 'react'
import { Modal, Col, Row, Form, Button, Tabs, Tab } from 'react-bootstrap'
import ReactSelect from 'react-select'

import AsyncSelect from 'react-select/async';
import ErrorBody from '../../App/components/Reusables/ErrorBody'
import { withRouter } from 'react-router-dom'
import { getPaymentMethods, createPayment, paybackCredit } from '../../api/payment/payment'
import Jhilikka from '../../App/components/Reusables/Jhilikka'
import { validatePaymentCreation, validateReedemGiftcard } from '../../validation/payment'
import { getAccounts } from '../../api/accounting/account'
import isEmpty from '../../validation/isEmpty'
import { reedemGiftCard } from '../../api/payment/giftCards';
import CustomSelectMenuList from '../../App/components/Reusables/CustomSelectMenuList';
import CustomSelectMenu from '../../App/components/Reusables/CustomSelectMenu';
import store from '../../redux/store';
import { RESET_FETCH_MORE_OPTIONS, SET_FETCH_MORE_OPTIONS, SET_IS_MENU_OPEN } from '../../redux/actions/types';
import DEMO from '../../globals/constant';

function MainModal(props) {
    const [accountOptions, setAccountOptions] = React.useState([])
    const [accountOptionsLoading, setAccountOptionsLoading] = React.useState(true);
    const [submitting, setSubmitting] = React.useState(false)
    const [methodsLoading, setmethodsLoading] = React.useState(true)
    const [paymentMethods, setPaymentMethods] = React.useState([]);
    // const [mode, setMode] = React.useState('payment')
    const [accountSearchMode, setAccountSearchMode] = React.useState('static');
    const [accountInputText, setAccountInputText] = React.useState('');
    const [defaultAccountOptions, setDefaultAccountOptions] = React.useState([]);
    const [accountOptionsMaxLength, setAccountOptionsMaxLength] = React.useState(0)


    const [paymentData, setPaymentData] = React.useState({
        invoice: null,
        purchaseOrder: null,

        accountId: '',
        accountName: '',

        creditedAccount: '',
        creditedAccountName: '',

        method: '',
        methodHeader: '',
        transactionFrom: '',
        amount: '',
        bankName: '',
        transactionId: '',
        remarks: '',

        code: '',


    })
    const [errors, setErrors] = React.useState({})

    //custom API call
    const getAccounts_ = async (signal) => {
        console.log('api call fired to get items')
        getAccounts({
            "action": 'get',
            "filter": 'none',
            "start": defaultAccountOptions.length,
            "end": defaultAccountOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then((res) => {
                if (res.json.status) {
                    setDefaultAccountOptions([
                        ...defaultAccountOptions,
                        ...res.json.accounts.map((itm, index) => ({ label: `${itm.name}`, value: itm.id }))
                    ]);
                    setAccountOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsAccs', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err)
            });
    }


    React.useEffect(() => {
        const controller = new AbortController()
        let tempData = {
            ...paymentData,
            amount: props.creditPaymentFor ? props.creditPaymentAmount : props.dueAmount
        }

        if (props.purchaseOrder) {
            tempData = { ...tempData, purchaseOrder: props.purchaseOrder.id }
        } else if (props.invoice) {
            tempData = { ...tempData, invoice: props.invoice.id, }
        }

        setPaymentData(tempData);
        getAccounts_(controller.signal);

        (async () => {


            let ladder = 0;

            await getPaymentMethods({ action: 'get' }, controller.signal)
                .then(res => {
                    console.log(res);
                    if (res.json.status) {
                        setPaymentMethods([...res.json.payment_methods])
                        setmethodsLoading(false)
                        ladder++;
                    } else {
                        Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                    }
                })
                .catch(err => {
                    console.log(err);
                })
            const accountsJson = { "action": "get", start: 0, end: 999999 };
            if (!isEmpty(props.purchaseOrder)) {
                console.log(props)
                accountsJson.filter = "vendor"
                accountsJson.vendor = props.purchaseOrder.vendor
            } else {
                accountsJson.filter = 'customer'
                accountsJson.customer = props.invoice.customer
            }
            console.log('here is accounts json', accountsJson)
            await getAccounts({ ...accountsJson }, controller.signal)
                .then((res) => {
                    console.log(res);
                    if (res.json.status) {
                        setAccountOptions([...res.json.accounts.map((ac, index) => ({ value: ac.id, label: ac.name }))])
                        setAccountOptionsLoading(false)
                        ladder++;
                    }
                }).catch((err) => {
                    console.log(err)
                });
        })()
        return () => controller.abort()
    }, []);

    const loadAccountOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getAccounts(requestJson)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    return res.json.accounts.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, uuid: itm.uuid }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }

    //onchange of the accout input text, update the account search mode
    React.useEffect(() => {
        if (accountInputText) {
            setAccountSearchMode('dynamic');
        } else {
            setAccountSearchMode('static')
        }
    }, [accountInputText])



    const _accountInputChange = (inputText) => {
        setAccountInputText(inputText);
    }

    const _scrollAccountMenuToBottom = (e) => {
        e.preventDefault();
        if (accountOptionsMaxLength > defaultAccountOptions.length && !accountInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsAccs', true]
            })
            getAccounts_();
        }
    }

    const _selectedAccountChange = (ac) => {
        setErrors({ ...errors, accountId: '' });
        console.log(ac)
        setPaymentData({ ...paymentData, accountId: ac.value, accountName: ac.label })
    }


    const _selectedCreditedAccountChange = (ac) => {
        setErrors({ ...errors, creditedAccount: '' });
        console.log(ac)
        setPaymentData({ ...paymentData, creditedAccount: ac.value, creditedAccountName: ac.label })
    }


    const _paymentDataChange = (e) => {
        console.log(e.target.value);
        const type = e.target.type;
        const name = e.target.name;
        const value = e.target.value;
        setErrors({ ...errors, [name]: '' })
        const tempPaymentData = paymentData;
        tempPaymentData[name] = type === 'number' ? parseFloat(value) : value
        if (name === 'method') {
            const typeData = paymentMethods.find(type => type.id == e.target.value);
            tempPaymentData['methodHeader'] = typeData.header;
        }
        setPaymentData({ ...tempPaymentData });
    }

    const _submit = () => {


        //do payback of credit if it's payback
        if (props.creditPaymentFor) {
            const { isValid, errors } = validatePaymentCreation(paymentData);
            if (!isValid) {
                console.log(errors)
                setErrors({ ...errors });
                return;
            };


            setSubmitting(true)
            const paybackJson = {
                "action": 'credit_payment',
                "credit_id": props.creditPaymentFor,
                "choosen_account": paymentData.accountId,
                "credited_account": paymentData.creditedAccount,
                "amount": paymentData.amount,
                "method": parseInt(paymentData.method),
                "bank_name": paymentData.bankName || null,
                "transaction_from": paymentData.transactionFrom || null,
                "transaction_id": paymentData.transactionId || null,
                "remarks": paymentData.remarks || null,
            }
            console.log(JSON.stringify(paybackJson))
            paybackCredit(paybackJson)
                .then((res) => {
                    console.log(res)
                    if (res.json.status) {
                        Jhilikka.fire({ icon: 'success', title: 'Credit paid back successfully!', background: '#dffff0' })
                        // props.onHide();
                        props.history.replace(props.match.url)
                    } else {
                        setSubmitting(false)
                        Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                    }
                }).catch((err) => {
                    setSubmitting(false)
                    console.log(err);
                });
            return;
        }


        const { isValid, errors } = validatePaymentCreation(paymentData);
        if (!isValid) {
            console.log(errors)
            setErrors({ ...errors });
            return;
        };


        setSubmitting(true)
        const requestJson = {
            "action": "add",
            "payments": [{
                "invoice": paymentData.invoice,
                "purchase_order": paymentData.purchaseOrder,
                "account": paymentData.accountId,
                "amount": paymentData.amount,
                "method": parseInt(paymentData.method),
                "bank_name": paymentData.bankName,
                "transaction_from": paymentData.transactionFrom,
                "transaction_id": paymentData.transactionId,
                "remarks": paymentData.remarks
            }],
        }


        console.log(JSON.stringify(requestJson));
        createPayment(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Payment added successfully!', background: '#dffff0' })
                    // props.onHide();
                    props.history.replace(props.match.url)
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err);
            });
    }


    const _reedem = () => {

        const { isValid, errors } = validateReedemGiftcard(paymentData);

        if (!isValid) {
            console.log(errors)
            setErrors({ ...errors });
            return;
        };

        //check if amount is greater than document's account
        let due = 0;

        setSubmitting(true)
        const requestJson = {
            "action": "redeeme",
            'code': paymentData.code,
            'customer': props.invoice.customer,
            'invoice': props.invoice.id,
            'account': paymentData.accountId,
        }


        console.log(JSON.stringify(requestJson));
        reedemGiftCard(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Giftcard redeemed successfully!', background: '#dffff0' })
                    // props.onHide();
                    props.history.replace(props.match.url)
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err);
            });
    }

    console.log(paymentData);

    return (
        <>
            <div className='paymentModalWrapper'>
                <Tabs variant="pills" defaultActiveKey="payment" >
                    <Tab eventKey="payment" title="PAYMENT">
                        <Row>
                            <Col md={6} >
                                <Form.Group  >
                                    <Form.Label>Account*</Form.Label> <br />
                                    <AsyncSelect
                                        defaultOptions={defaultAccountOptions}
                                        value={paymentData.accountId ? { label: paymentData.accountName, value: paymentData.accountId } : null}
                                        onChange={_selectedAccountChange}
                                        loadOptions={loadAccountOptions}
                                        // placeholder={accountOptionsLoading ? 'Loading...' : 'Select...'}
                                        onInputChange={_accountInputChange}
                                        components={{
                                            MenuList: CustomSelectMenuList,
                                        }}
                                        onMenuScrollToBottom={_scrollAccountMenuToBottom}
                                        onMenuClose={() => {
                                            store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', false] })
                                        }}
                                        onMenuOpen={() => {
                                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', true] })
                                        }}
                                    />
                                    <ErrorBody>{errors.accountId}</ErrorBody>
                                </Form.Group>
                            </Col>

                            {props.creditPaymentFor ?
                                <Col md={6} >
                                    <Form.Group  >
                                        <Form.Label>Credited Account*</Form.Label> <br />
                                        <AsyncSelect
                                            value={paymentData.creditedAccount ? { label: paymentData.creditedAccountName, value: paymentData.creditedAccount } : null}
                                            onChange={_selectedCreditedAccountChange}
                                            loadOptions={loadAccountOptions}
                                            // placeholder={accountOptionsLoading ? 'Loading...' : 'Select...'}

                                            defaultOptions={defaultAccountOptions}
                                            onInputChange={_accountInputChange}
                                            components={{
                                                MenuList: CustomSelectMenuList,
                                            }}
                                            onMenuScrollToBottom={_scrollAccountMenuToBottom}
                                            onMenuClose={() => {
                                                store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', false] })
                                            }}
                                            onMenuOpen={() => {
                                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', true] })
                                            }}

                                        />
                                        <ErrorBody>{errors.creditedAccount}</ErrorBody>
                                    </Form.Group>
                                </Col> :
                                null
                            }

                            <>
                                <Col md={6} >
                                    <Form.Group  >

                                        <Form.Label>Payment Method*</Form.Label> <br />
                                        <Form.Control
                                            name="method"
                                            value={paymentData.method}
                                            onChange={(e) => _paymentDataChange(e)}
                                            type={'number'}
                                            as="select"

                                        >
                                            <option value='' disabled >{methodsLoading ? 'Loading...' : 'Select...'} </option>
                                            {paymentMethods.map((method, index) => {
                                                return <option key={index} value={method.id}   >{method.name}</option>
                                            })}
                                        </Form.Control>
                                        <ErrorBody>{errors.method}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6} >
                                    <Form.Group  >

                                        <Form.Label>Amount*</Form.Label> <br />
                                        <Form.Control
                                            name="amount"
                                            value={paymentData.amount || ''}
                                            onChange={(e) => _paymentDataChange(e)}
                                            type="number"
                                            placeholder="Amount"
                                        />
                                        <ErrorBody>{errors.amount}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6} >
                                    <Form.Group  >

                                        <Form.Label>Transaction From</Form.Label> <br />
                                        <Form.Control
                                            name="transactionFrom"
                                            value={paymentData.transactionFrom}
                                            onChange={(e) => _paymentDataChange(e)}
                                            type="text"
                                            placeholder="Transaction From"
                                        />
                                        <ErrorBody>{errors.transactionFrom}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                {paymentData.methodHeader === 'bank' ?
                                    <Col md={6} >
                                        <Form.Group  >
                                            <Form.Label>Bank Name*</Form.Label> <br />
                                            <Form.Control
                                                name="bankName"
                                                value={paymentData.bankName}
                                                onChange={(e) => _paymentDataChange(e)}
                                                type="text"
                                                placeholder="Bank Name"
                                            />

                                            <ErrorBody>{errors.bankName}</ErrorBody>
                                        </Form.Group>
                                    </Col> :
                                    null}


                                <Col md={6} >
                                    <Form.Group  >
                                        <Form.Label>Transaction ID</Form.Label> <br />
                                        <Form.Control
                                            name="transactionId"
                                            value={paymentData.transactionId || ''}
                                            onChange={(e) => _paymentDataChange(e)}
                                            type="text"
                                            placeholder="Transaction ID"
                                        />
                                        <ErrorBody>{errors.transactionId}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6} >
                                    <Form.Group  >

                                        <Form.Label>Remarks</Form.Label> <br />
                                        <Form.Control
                                            name="remarks"
                                            as="textarea"
                                            rows="3"
                                            onChange={(e) => _paymentDataChange(e)}
                                            placeholder="Remarks"
                                        />
                                        <ErrorBody>{errors.remarks}</ErrorBody>
                                    </Form.Group>
                                </Col>
                            </>
                        </Row>

                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                            <Button variant="secondary" onClick={props.onHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" onClick={_submit} disabled={submitting}>
                                {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                            </Button>
                        </div>




                    </Tab>
                    {!props.invoice ? null :
                        <Tab eventKey="giftcard" title="REDEEM GIFT CARD">
                            <Row>
                                <Col md={6} >
                                    <Form.Group  >
                                        <Form.Label>Account*</Form.Label> <br />
                                        <AsyncSelect
                                            value={paymentData.accountId ? { label: paymentData.accountName, value: paymentData.accountId } : null}
                                            onChange={_selectedAccountChange}
                                            loadOptions={loadAccountOptions}
                                        // placeholder={accountOptionsLoading ? 'Loading...' : 'Select...'}
                                        />
                                        <ErrorBody>{errors.accountId}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6} >
                                    <Form.Group  >

                                        <Form.Label>Code</Form.Label> <br />
                                        <Form.Control
                                            name="code"
                                            value={paymentData.code || ''}
                                            onChange={(e) => _paymentDataChange(e)}
                                            type="text"
                                            placeholder="Code"
                                        />
                                        <ErrorBody>{errors.code}</ErrorBody>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                                <Button variant="secondary" onClick={props.onHide}>
                                    Cancel
                            </Button>
                                <Button variant="primary" onClick={_reedem} disabled={submitting}>
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>
                        </Tab>
                    }
                </Tabs>
            </div>
        </>
    )
}

const CreatePaymentModal = (props) => {
    return <Modal
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
        show={props.show}
        onHide={props.onHide}
    >
        <MainModal
            show={props.show}
            onHide={props.onHide}
            purchaseOrder={props.purchaseOrder || null}
            invoice={props.invoice || null}
            dueAmount={props.dueAmount}
            creditPaymentFor={props.creditPaymentFor}
            {...props}
        />
    </Modal>
}

export default withRouter(CreatePaymentModal);
