import React from 'react'
import { Card } from 'react-bootstrap'

function PaymentInfo(props) {

    let info = null

    if (props.paidAmount >= props.billAmount) {
        //paid
        info = <Card.Header className="rounded-top text-dark text-right pt-3 pb-3" style={{ backgroundColor: '#96dbff' }}>
            <b>Paid</b>
        </Card.Header>
    }

    if (props.paidAmount < props.billAmount && props.paidAmount > 0) {
        //partially paid
        info = <Card.Header className="rounded-top text-dark text-right pt-3 pb-3" style={{ backgroundColor: '#ffd678' }}>
            <b>Partially Paid</b>
        </Card.Header>
    }

    if (props.isCanceled) {
        info = <Card.Header className="rounded-top text-dark text-right pt-3 pb-3" style={{ backgroundColor: '#ffb09d' }}>
            <b>Invoice Canceled</b>
            <br/>
            <b>Reason:</b> {props.cancelationReason}
        </Card.Header>
    }



    return info;
}

export default PaymentInfo
